














import Vue from 'vue'
export default Vue.extend({
  name: 'search',
  data: () => ({
    inputValue: '' as string,
    selectValue: 'песня' as string
  }),
  methods: {
    getTextInput (): void {
      if (this.inputValue) {
        this.$emit('sendText', {
          select: this.selectValue,
          value: this.inputValue
        })
      }
      (this.$el as any).reset()
    }
  }
})
